.bottom-sheet {
    @apply bg-white fixed left-0 w-full border-0 outline-none rounded-tl-lg rounded-tr-lg;
    bottom: -100%;
    height: 40vh;
    transition: bottom 0.3s ease;
    z-index: 9999;
  }
  
  .bottom-sheet.open {
    @apply bottom-0;
  }
  
  .bottom-sheet-content {
    @apply px-3 overflow-y-auto h-full py-2;
  }
  .bottom-sheet-header{
    @apply p-3 relative;
   border-bottom: .5px solid rgba(128, 128, 128, 0.623);    
  }
  
  .bottom-sheet-overlay {
    @apply fixed top-0 left-0 h-full w-full opacity-0 pointer-events-none transition-opacity duration-300;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9998;
  }
  
  .bottom-sheet-overlay.open {
    opacity: 1;
    pointer-events: auto;
  }