.drawer {
    position  : fixed;
    top       : 0;
    right     : 0;
    bottom    : 0;
    left      : 0;
    z-index   : 1000;
    overflow  : hidden;
    visibility: hidden;
    opacity   : 0;
    transition: visibility 0s, opacity 0.3s ease-in-out;
}

.drawer.open {
    visibility: visible;
    opacity   : 1;
}

.drawer-overlay {
    position        : fixed;
    top             : 0;
    right           : 0;
    bottom          : 0;
    left            : 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index         : 1000;
}

.drawer-container {
    position                  : absolute;
    top                       : 0;
    left                      : 0;
    bottom                    : 0;
    width                     : 80%;
    background-color          : #fff;
    box-shadow                : 0 0 10px rgba(0, 0, 0, 0.5);
    z-index                   : 1001;
    overflow-y                : auto;
    transform                 : translateX(-100%);
    transition                : transform 0.3s ease-in-out;
    border-top-right-radius   : 15px;
    border-bottom-right-radius: 15px;
}

.drawer-container:not(.open) {
    transition: all 0.3s ease-in-out;
}

.drawer.open .drawer-container {
    transform: translateX(0%);
}

.active-nav-item {
    @apply overflow-hidden rounded-md bg-blue-500 bg-opacity-75 text-white font-bold transition-all duration-200 ease-in-out;

}