.grid-gallery {
    @apply px-16;
    width: 100%;
    column-count: 4;
}
.card {
    box-sizing: border-box;
    @apply bg-white my-4 shadow-md rounded-2xl overflow-hidden;
    background-size: 100% 100%;
    background-repeat: no-repeat;
}


@media screen  and (max-width:768px){
    .grid-gallery {
        @apply px-8;
        column-count: 2;
    }
}

@media screen  and (max-width:512px){
    .grid-gallery {
        @apply px-4;
        column-count: 1;
    }
}