.responsive-form {
    @apply w-full p-5 bg-gray-100 justify-evenly items-center md:px-36;
}

.title {
    @apply w-full mb-3 text-xl font-bold text-center text-blue-500;
}

.layer {
    @apply absolute top-0 h-full w-full transition-all duration-700 ease-in-out opacity-0 invisible text-center p-4 m-auto  flex flex-col justify-center;
    background-color: #3b83f68c;
}

figure.grid-gallery__item:hover>div.layer {
    @apply visible opacity-100;
}

figure.grid-gallery__item:hover>img {
    @apply scale-125 transition-transform duration-700 ease-in-out;
}


figure.grid-gallery__item:hover>div.layer h3 {
    @apply text-white font-bold mt-0 mb-3;
}

figure.grid-gallery__item>div.layer h3 {
    @apply text-white font-bold transition-all duration-700 ease-in-out text-center;
    margin-top: 30px;
    margin-bottom: 80px;
}


figure.grid-gallery__item>div.layer p {
    @apply text-white text-sm transition-all duration-700 ease-in-out text-center;
}

.c {
    fill: none;
    stroke: #FFFFFF;
    stroke-linecap: round;
    stroke-linejoin: round;
}