.slider-main{
  display: inline;
  max-width: 80%;
}

.slider-main h1{
  font: bold 38px Arial, sans-serif;
  color: #f8ff2c;
}

.slider-main p{
  color: #ffffff;
  font: bold 20px Arial, sans-serif;
  margin: 4px;
}

/*Button inscribirse!*/

.slider-main .button_inscription{
  background-color: #f6ff48;
  color: rgb(26, 26, 26);
  margin-top: 10px;
  margin-right: 8px;
}

.slider-main .button_inscription:hover{
  background-color: #eef615;
}

/*Button ya inscritos!*/

.slider-main .button_already_registered{
  background-color: #fcffea;
  color: rgb(26, 26, 26);
  margin-top: 10px;
  margin-right: 6px;
}

.slider-main .button_already_registered:hover{
  background-color: #f5f6ed;
}

@media (min-width: 668px) {
  /* Estilos para pantallas medianas */
  .slider-main h1{
    font-size: 68px;
  }
  .slider-main p{
    font-size: 30px;
  }
  .slider-main{
    margin: 80px;
  }
  .slider-main .button_already_registered, .button_inscription{
    padding: 11px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

/* Animaciones de texto de header */
@keyframes texto-h1 {
  0% { content: "DIVERSIÓN"; }
  25% { content: "RESPETO"; }
  50% { content: "DEPORTE"; }
  75% { content: "REGALOS"; }
  100% { content: "SALUD"; }
}

@keyframes texto-p {
  0% { content: "Disfruta de una atmósfera distendida."; }
  25% { content: "En un ambiente sano y cordial."; }
  50% { content: "Compite y, si puedes, gana!"; }
  75% { content: "Excelente calidad. Inscripciones, VIP y premios."; }
  100% { content: "Combate la vida sedentaria!"; }
}

@keyframes fade {
  from { opacity: 0; }
  to { opacity: 1; }
}

.slider-main h1::before {
  content: attr(data-content);
  animation: texto-h1 26s linear infinite, fade 6s infinite; /* Añadimos ambas animaciones separadas por coma */
  opacity: 0;
  transition: opacity 1s linear;
}

.slider-main p::before {
  content: attr(data-content);
  animation: texto-p 26s linear infinite;
}

.slider-main h1:hover::before {
  opacity: 0;
}
